import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Settings, Zap, ZapOff, Monitor, Smartphone, Cpu } from 'lucide-react';
import { useLocalStorage } from '@/hooks';
import { cn } from '@/utils';

interface PerformanceSettings {
  enableAnimations: boolean;
  enableMouseTrail: boolean;
  enableFallingBinary: boolean;
  enableHeavyEffects: boolean;
  performanceMode: 'auto' | 'high' | 'balanced' | 'performance';
}

const PerformanceSettings: React.FC = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [settings, setSettings] = useLocalStorage<PerformanceSettings>('performance-settings', {
    enableAnimations: true,
    enableMouseTrail: true,
    enableFallingBinary: true,
    enableHeavyEffects: true,
    performanceMode: 'auto'
  });

  const [deviceInfo, setDeviceInfo] = useState({
    isMobile: false,
    isLowEnd: false,
    memory: 0,
    cores: 0
  });

  useEffect(() => {
    const isMobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
    const cores = navigator.hardwareConcurrency || 4;
    const memoryInfo = (performance as any).memory;
    const memory = memoryInfo ? Math.round(memoryInfo.jsHeapSizeLimit / 1024 / 1024 / 1024) : 0;
    const isLowEnd = cores <= 4 || memory < 2;

    setDeviceInfo({ isMobile, isLowEnd, memory, cores });

    // Auto-adjust settings based on device
    if (settings.performanceMode === 'auto') {
      if (isMobile && isLowEnd) {
        setSettings(prev => ({
          ...prev,
          enableMouseTrail: false,
          enableFallingBinary: false,
          enableHeavyEffects: false
        }));
      }
    }
  }, [settings.performanceMode, setSettings]);

  const presets = {
    high: {
      enableAnimations: true,
      enableMouseTrail: true,
      enableFallingBinary: true,
      enableHeavyEffects: true,
      performanceMode: 'high' as const
    },
    balanced: {
      enableAnimations: true,
      enableMouseTrail: true,
      enableFallingBinary: false,
      enableHeavyEffects: false,
      performanceMode: 'balanced' as const
    },
    performance: {
      enableAnimations: false,
      enableMouseTrail: false,
      enableFallingBinary: false,
      enableHeavyEffects: false,
      performanceMode: 'performance' as const
    }
  };

  const applyPreset = (preset: keyof typeof presets) => {
    setSettings(presets[preset]);
  };

  const toggleSetting = (key: keyof PerformanceSettings) => {
    setSettings(prev => ({
      ...prev,
      [key]: !prev[key],
      performanceMode: 'auto' // Reset to auto when manually changing settings
    }));
  };

  return (
    <>
      {/* Settings Button */}
      <motion.button
        onClick={() => setIsOpen(true)}
        className="fixed bottom-4 right-4 z-40 p-3 bg-dark-200/80 backdrop-blur-md border border-primary/20 rounded-full text-primary hover:bg-dark-200 hover:border-primary/40 transition-all duration-300"
        whileHover={{ scale: 1.05 }}
        whileTap={{ scale: 0.95 }}
        title="Performance Settings"
      >
        <Settings size={20} />
      </motion.button>

      {/* Settings Modal */}
      <AnimatePresence>
        {isOpen && (
          <>
            {/* Backdrop */}
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
              className="fixed inset-0 z-50 bg-black/50 backdrop-blur-sm"
              onClick={() => setIsOpen(false)}
            />

            {/* Modal */}
            <motion.div
              initial={{ opacity: 0, scale: 0.9, y: 20 }}
              animate={{ opacity: 1, scale: 1, y: 0 }}
              exit={{ opacity: 0, scale: 0.9, y: 20 }}
              className="fixed top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 z-50 w-full max-w-md mx-4"
            >
              <div className="bg-dark-200/95 backdrop-blur-lg border border-primary/20 rounded-2xl p-6 shadow-2xl">
                <div className="flex items-center justify-between mb-6">
                  <h3 className="text-xl font-bold text-white flex items-center gap-2">
                    <Zap className="text-primary" size={24} />
                    Performance Settings
                  </h3>
                  <button
                    onClick={() => setIsOpen(false)}
                    className="text-white/60 hover:text-white transition-colors"
                  >
                    
                  </button>
                </div>

                {/* Device Info */}
                <div className="mb-6 p-4 bg-white/5 rounded-lg border border-white/10">
                  <h4 className="text-sm font-semibold text-white mb-2 flex items-center gap-2">
                    {deviceInfo.isMobile ? <Smartphone size={16} /> : <Monitor size={16} />}
                    Device Information
                  </h4>
                  <div className="text-xs text-white/70 space-y-1">
                    <div className="flex justify-between">
                      <span>Device Type:</span>
                      <span>{deviceInfo.isMobile ? 'Mobile' : 'Desktop'}</span>
                    </div>
                    <div className="flex justify-between">
                      <span>CPU Cores:</span>
                      <span>{deviceInfo.cores}</span>
                    </div>
                    {deviceInfo.memory > 0 && (
                      <div className="flex justify-between">
                        <span>Memory:</span>
                        <span>~{deviceInfo.memory}GB</span>
                      </div>
                    )}
                    <div className="flex justify-between">
                      <span>Performance:</span>
                      <span className={cn(
                        deviceInfo.isLowEnd ? 'text-yellow-400' : 'text-green-400'
                      )}>
                        {deviceInfo.isLowEnd ? 'Limited' : 'Good'}
                      </span>
                    </div>
                  </div>
                </div>

                {/* Quick Presets */}
                <div className="mb-6">
                  <h4 className="text-sm font-semibold text-white mb-3">Quick Presets</h4>
                  <div className="grid grid-cols-3 gap-2">
                    <button
                      onClick={() => applyPreset('high')}
                      className={cn(
                        'p-3 rounded-lg text-xs font-medium transition-all duration-300 border',
                        settings.performanceMode === 'high'
                          ? 'bg-primary/20 text-primary border-primary/30'
                          : 'bg-white/5 text-white/70 border-white/10 hover:bg-white/10'
                      )}
                    >
                      <Zap size={16} className="mx-auto mb-1" />
                      High Quality
                    </button>
                    <button
                      onClick={() => applyPreset('balanced')}
                      className={cn(
                        'p-3 rounded-lg text-xs font-medium transition-all duration-300 border',
                        settings.performanceMode === 'balanced'
                          ? 'bg-primary/20 text-primary border-primary/30'
                          : 'bg-white/5 text-white/70 border-white/10 hover:bg-white/10'
                      )}
                    >
                      <Cpu size={16} className="mx-auto mb-1" />
                      Balanced
                    </button>
                    <button
                      onClick={() => applyPreset('performance')}
                      className={cn(
                        'p-3 rounded-lg text-xs font-medium transition-all duration-300 border',
                        settings.performanceMode === 'performance'
                          ? 'bg-primary/20 text-primary border-primary/30'
                          : 'bg-white/5 text-white/70 border-white/10 hover:bg-white/10'
                      )}
                    >
                      <ZapOff size={16} className="mx-auto mb-1" />
                      Performance
                    </button>
                  </div>
                </div>

                {/* Individual Settings */}
                <div className="space-y-3">
                  <h4 className="text-sm font-semibold text-white">Individual Settings</h4>
                  
                  {[
                    { key: 'enableAnimations', label: 'Smooth Animations', description: 'Page transitions and hover effects' },
                    { key: 'enableMouseTrail', label: 'Mouse Trail', description: 'Animated particles following cursor' },
                    { key: 'enableFallingBinary', label: 'Falling Binary', description: 'Matrix-style background animation' },
                    { key: 'enableHeavyEffects', label: 'Heavy Effects', description: 'Glow effects and complex shadows' }
                  ].map(({ key, label, description }) => (
                    <div key={key} className="flex items-center justify-between p-3 bg-white/5 rounded-lg">
                      <div className="flex-1">
                        <div className="text-sm font-medium text-white">{label}</div>
                        <div className="text-xs text-white/60">{description}</div>
                      </div>
                      <button
                        onClick={() => toggleSetting(key as keyof PerformanceSettings)}
                        className={cn(
                          'w-12 h-6 rounded-full transition-all duration-300 relative',
                          settings[key as keyof PerformanceSettings]
                            ? 'bg-primary'
                            : 'bg-white/20'
                        )}
                      >
                        <div
                          className={cn(
                            'w-5 h-5 bg-white rounded-full absolute top-0.5 transition-all duration-300',
                            settings[key as keyof PerformanceSettings]
                              ? 'left-6'
                              : 'left-0.5'
                          )}
                        />
                      </button>
                    </div>
                  ))}
                </div>

                {/* Recommendation */}
                {deviceInfo.isLowEnd && (
                  <div className="mt-4 p-3 bg-yellow-500/10 border border-yellow-500/20 rounded-lg">
                    <div className="text-xs text-yellow-400 font-medium mb-1">
                       Recommendation
                    </div>
                    <div className="text-xs text-yellow-300/80">
                      Your device may benefit from the "Performance" preset for smoother experience.
                    </div>
                  </div>
                )}
              </div>
            </motion.div>
          </>
        )}
      </AnimatePresence>
    </>
  );
};

export default PerformanceSettings;